/* #region MOBILE MENU ICON ============================================================= */
.hamburger {
  position: absolute;
  top: 50%;
  right: calc((4rem - calc(4rem * 0.5)) / 2 + 0);
  transform: translate(0, -50%);
  width: calc(4rem * 0.5);
  height: calc(4rem * 0.5);
  margin: 0;
  padding: 0;
  font-size: 0;
  text-indent: -9999px;
  appearance: none;
  box-shadow: none;
  border: none;
  cursor: pointer;
  /* active state, i.e. menu opened */
}
.hamburger:focus {
  outline: none;
}
.hamburger:before {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 4px;
  content: " ";
  display: block;
  width: 4rem;
  height: 4rem;
  position: absolute;
  z-index: 0;
  background: red;
}
.hamburger span {
  border-radius: 1.5px;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 2px;
  width: 100%;
  background: var(--primary);
  transition: all 0.3s;
}
.hamburger span:before, .hamburger span:after {
  border-radius: 1.5px;
  content: "";
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--primary);
}
.hamburger span:before {
  top: -6px;
}
.hamburger span:after {
  bottom: -6px;
}
.hamburger--htx {
  background: transparent;
}
.hamburger--htx span:before, .hamburger--htx span:after {
  transition-duration: 0.3s;
}
.hamburger--htx span:before {
  transition-property: top, transform;
}
.hamburger--htx span:after {
  transition-property: bottom, transform;
}
.hamburger--htx.is-active {
  background: transparent;
}
.hamburger--htx.is-active span {
  background: none;
  transition: none;
}
.hamburger--htx.is-active span:before {
  top: 0;
  transform: rotate(45deg);
}
.hamburger--htx.is-active span:after {
  bottom: 0;
  transform: rotate(-45deg);
}
.hamburger--htx.is-active span:before, .hamburger--htx.is-active span:after {
  transition-delay: 0s, 0.3s;
}

